.header {
  height: 720px;
  position: relative;
  color: #fff;
  overflow: hidden; }
  .header .banner {
    height: 100%;
    display: flex; }
    .header .banner img {
      width: 100%;
      height: 100%;
      object-fit: cover; }
    .header .banner::after {
      content: '';
      display: block;
      background: rgba(0, 0, 0, 0.4);
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0; }
  .header .nav {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    height: 72px;
    justify-content: space-between;
    align-items: center; }
    .header .nav a {
      color: #fff;
      margin-left: 30px;
      text-decoration: none; }
  .header .content {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    position: absolute;
    top: 50px;
    bottom: 0;
    right: 0;
    left: 0;
    padding-top: 100px;
    padding-bottom: 100px;
    text-align: center; }
    .header .content button {
      color: #fff;
      background: linear-gradient(140deg, #ff2a86 -20%, #6ab8dd 150%, #30f0ff 200%);
      padding: 20px 30px;
      border-radius: 48px; }

@media screen and (max-width: 480px) {
  .header .content {
    bottom: 0;
    top: auto;
    padding-bottom: 30px; } }
